.social-share {
  @include reset-list;
  display: flex;
  margin: 0;
}

.social-share__item {
  display: inline-block;
  margin: 0;

  + .social-share__item {
    padding-left: unit(1.5);
  }

  a {
    @include hover-underline;
    background-position: 0 50%;
    background-repeat: no-repeat;
    color: $color--jelly-bean;
    display: block;
    font-size: 1.2rem;
    line-height: 2rem;

    .hero__details & {
      color: $color--white;
    }
  }

  &.-facebook a {
    background-image: url(#{$path}/images/share--facebook.svg);
    background-position: 0 calc(50% - 1px);
    padding-left: 14px;

    .hero__details & {
      background-image: url(#{$path}/images/share--facebook-white.svg);
    }
  }

  &.-twitter a {
    background-image: url(#{$path}/images/share--twitter.svg);
    padding-left: 20px;

    .hero__details & {
      background-image: url(#{$path}/images/share--twitter-white.svg);
    }
  }

  &.-linkedin a {
    background-image: url(#{$path}/images/share--linkedin.svg);
    background-position: 0 calc(50% - 2px);
    padding-left: 19px;

    .hero__details & {
      background-image: url(#{$path}/images/share--linkedin-white.svg);
    }
  }

  &.-email a {
    background-image: url(#{$path}/images/share--email.svg);
    padding-left: 26px;

    .hero__details & {
      background-image: url(#{$path}/images/share--email-white.svg);
    }
  }
}
